.header-color {
    background: #acb6e5;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #86fde8, #acb6e5);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #86fde8, #acb6e5); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

   
  }
  .HomePage-grid {
    background: #acb6e5;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #86fde8, #acb6e5);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #86fde8, #acb6e5); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    
    text-align: center;
    position:fixed;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;

  }
.avatar-img {
 height: 300px;
 padding-top: 4rem;
 padding-bottom:5rem;
 border-radius: 50%;
}
.bannerz-text{
    color:white;
    font-family: sans-serif;
    padding-top:2rem;
}
.banner-text{
    background-color:black;
    opacity: .6;
    width: 75%;
    margin: auto;
    border-radius: 10px;
}
.banner-text h1{
    font: 66px;
    font-weight:bold;
    color: white;

}
.banner-text hr{
    border-top: 5px dotted white;
    width: 50%;
    margin: auto;

}
.contact-body{
 margin:auto;
 background: white;

 position:fixed;
 width:100%;
 height:100%;
 justify-content: center;
 padding-top:2em;

}
.contact-grid{
    text-align: center;
    justify-content: center;
    width: 80%;
    height:650px;
    background: white;
  
    margin-bottom: 20px;

}
.social-links {
    display: flex;
    justify-content: space-between;
    width: 50%;
    margin: auto;
}
.social-links i {
    color: white;
    font-size: 5em;
    padding:8px;
    
}
.banner-text p{
    color: white;
    font-size: 30px;
    padding: 8px;
}
.contact-grid h2{
    font-family:'Source Serif Pro', sans-serif;
}
.contact-grid p{
    font-family:'Source Serif Pro',sans-serif;
    font-weight: bold;
    font-size:15px;
}
.contact-grid hr{
    border-top: 5px dotted black;
    width:200px;
    margin: auto;
}
.contact-list i{
    font-size: 45px;
    padding-right: 6rem;
    padding-left: 5rem;
    color: black;
    
  
}
.projects-grid {
    display:flex;
    padding-top: 100px;
}
.resume-right-col{
    background:lightblue;
    opacity:90%;
    padding:2em;
    color:white;
}
